body #fonts {
  margin: 0;
  padding: 0;
  font-family: 'S-CoreDream-3Light';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  font-family: 'S-CoreDream-3Light';
  background-color: #f2f2f2;
}

@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff')
    format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'silgothic';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_eight@1.0/silgothic.woff')
    format('woff');
  font-weight: normal;
  font-style: normal;
}

.studentSignUp {
  text-align: center;
}

.logo {
  height: 15rem;
  min-height: 300px;
  pointer-events: none;
}

.userStudentSignUp {
  height: 8rem;
  margin-left: 10px;
  padding-right: 15px;
  pointer-events: none;
  justify-content: center;
  float: left;
}
.signUpInput {
  flex-direction: column;
  height: 200px;
  min-width: 100px;
  width: 10em;
  font-size: 0.4em;
  padding-left: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

div.inputCheck > button {
  min-width: 60px;
  margin-left: 5px;
}

.signUp {
  width: 15em;
  height: 10em;
  min-height: 270px;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.header {
  background-color: white;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.studentSignUpBox {
  background-color: rgb(247, 207, 192);
  box-shadow: 5px 5px 5px darkgrey;
  height: 30rem;
  min-height: 350px;
  min-width: 250px;
  width: 35rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(173, 5, 5);
  padding-top: 20px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 50px;
}

.signUpButton {
  width: 30vh;
  min-width: 200px;
  min-height: 50px;
  align-items: center;
  justify-content: center;
}

.welcomeText {
  box-shadow: 5px 5px 5px darkgray;
  background-color: rgb(252, 230, 222);
  min-height: 30px;
  min-width: 480px;
  height: 1rem;
  width: 35rem;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5vh;
  color: gray;
}

.inputCheck {
  min-width: 150px;
  flex: 1 1;
  margin-left: 100px;
}

.input {
  min-width: 130px;
  margin-left: 45px;
}

.login {
  text-align: center;
}

.logo {
  height: 10em;
  pointer-events: none;
}

.userLogin {
  height: 100px;
  pointer-events: none;
}

.header {
  background-color: white;
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.mainBox {
  background-color: rgb(247, 207, 192);
  box-shadow: 5px 5px 5px darkgray;
  height: 15rem;
  min-height: 250px;
  width: 30rem;
  min-width: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(173, 5, 5);
  padding-top: 10px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}

.checkBox {
  box-shadow: 5px 5px 5px darkgray;
  background-color: rgb(252, 230, 222);
  height: 3em;
  width: 12rem;
  min-width: 150px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding-left: 30px;
  padding-right: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 15px;
  color: gray;
}

.input {
  flex-direction: column;
  height: 10vh;
  padding-left: 10px;
  padding-right: 1vh;
  display: flex;
  align-items: center;
  font-size: 10px;
  justify-content: flex-start;
}

.inputID {
  height: 100px;
  padding-left: 20px;
  padding-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
}

.inputPW {
  height: 5vh;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
}

.loginHeader {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.loginInput {
  flex-direction: row;
  height: 10vh;
  padding-top: 45px;
  padding-left: 10px;
  padding-right: 1vh;
  display: flex;
  align-items: center;
  font-size: 10px;
  justify-content: flex-start;
}

div.loginSignUp {
  height: 10vh;
  width: 20rem;
  min-width: 300px;
  display: inline-block;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  margin: 10px;
}

.mapping {
    background-color: rgb(255, 255, 255);
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  }
  
  .Information{
    background-color: rgb(255, 255, 255);
    box-shadow: 5px 5px 5px darkgray;
    border: 2px solid rgb(247, 207, 192);
    height: 60vh;
    width: 30vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(78, 73, 73);
    margin: 1vh;
    border-radius: 10px;
  }

  .PayLog{
    background-color: rgb(255, 255, 255);
    box-shadow: 5px 5px 5px darkgray;
    border: 2px solid rgb(247, 207, 192);
    height: 50vh;
    width: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(78, 73, 73);
    border-radius: 10px;
    margin: 1vh;
  }

  .test{
    align-items: center;
    justify-content: center;
  }
